.preview-image-container {
    width: 270px;
    height: 200px;
    margin: 0 auto;
}

.preview-image {
    height: 100%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    background-size: cover;
    background-position: center;
}

.preview-image-tooltip {
    top: 10px;
    right: 10px;
    width: 24px;
    height: 24px;
    display: -webkit-flex;
    display: flex;
    position: absolute;
    border-radius: 50%;
    background-size: cover;
    background-color: white;
    border: 2px solid white;
    background-position: center;
    background-image: url(data:image/svg+xml;utf8;base64,);
}

.preview-image-tooltip.active::after {
    top: 30px;
    right: 10px;
    width: 225px;
    display: -webkit-flex;
    display: flex;
    position: absolute;
    border-radius: 4px;
    content: attr(title);
    background: #303030;
    border: 1px solid #303030;
}
.pagination-controls {
