}

.pagination-controls .icon {
    width: 14px;
    height: 14px;
    cursor: pointer;
    display: -webkit-inline-flex;
    display: inline-flex;
    background-size: contain;
    background-repeat: no-repeat;
}

.pagination-controls .icon.next {
